import { OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { BaseFilePreview } from '../base-file-preview';
const FIVE_MB = 5242880;
export class TextPreviewComponent extends BaseFilePreview {
    constructor() {
        super(...arguments);
        this.content$ = new BehaviorSubject('');
        this.fileTooLarge$ = new BehaviorSubject(false);
    }
    ngOnInit() {
        if (this.file.file_size > FIVE_MB) {
            this.fileTooLarge$.next(true);
        }
        else {
            this.loadFileContents();
        }
    }
    loadFileContents() {
        this.getContents()
            .subscribe(content => {
            this.content$.next(content);
        });
    }
}
